export default {"id":"x84cIsRK","block":"{\"symbols\":[\"@swagger\"],\"statements\":[[6,\"h1\"],[7],[0,\"Welcome to the \"],[1,[19,1,[\"info\",\"title\"]],false],[0,\" API!\"],[8],[0,\"\\n\\n\"],[6,\"div\"],[9,\"class\",\"col-md-9\"],[7],[0,\"\\n  \"],[6,\"div\"],[9,\"class\",\"introduction\"],[7],[0,\"\\n    \"],[6,\"p\"],[7],[0,\"\\n      This API adheres to the open-source \"],[6,\"a\"],[9,\"target\",\"_blank\"],[9,\"href\",\"http://jsonapi.org\"],[7],[0,\"JSONAPI Specification\"],[8],[0,\". If you're unfamiliar with JSONAPI, start there!\\n    \"],[8],[0,\"\\n\\n    \"],[6,\"p\"],[7],[0,\"\\n      Because the specification is verbose, we've omitted the cruft. Instead, Resource Schemas detail the relevant attributes, types, and descriptions.\\n      At the top of each endpoint's documentation, you'll see the schemas relevant to that endpoint (ie, possible sideloads and sideposts).\\n    \"],[8],[0,\"\\n\\n    \"],[6,\"p\"],[7],[0,\"\\n      If you need a token to access this API, use something like \"],[6,\"a\"],[9,\"target\",\"_blank\"],[9,\"href\",\"http://www.requestly.in\"],[7],[0,\"Requestly\"],[8],[0,\" to modify your headers each request. This way you can access endpoints directly through your browser.\\n    \"],[8],[0,\"\\n  \"],[8],[0,\"\\n\\n  \"],[6,\"div\"],[9,\"class\",\"description\"],[7],[0,\"\\n    \"],[6,\"h2\"],[7],[0,\"API Details\"],[8],[0,\"\\n    \"],[1,[19,1,[\"info\",\"description\"]],true],[0,\"\\n  \"],[8],[0,\"\\n\"],[8],[0,\"\\n\"]],\"hasEval\":false}","meta":{"specifier":"template:/jsonapi-swagger-ui/components/index-route"}};